  const singleCourse = async () => {
    try {
      if (id !== null) {
        setLoading(true);
        const response = await getSingleCourse(
          id,
          preferences?.data?.currency || currency || "INR"
        );
        if (response) {
          setSingleCourseData(response?.data?.data);
        } else {
          console.log("Error");
        }
        setLoading(false);
      }
    } catch (error) {
      setLoading(false);
    }
  };
  useEffect(() => {
    if (id !== "null") {
      singleCourse();
    }
    // eslint-disable-next-line
  }, [id]);

